import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';

import { setError } from './errorSlice';
import axios from 'axios';


const initialState = {
    allPagesServer: '',
    currentPageNumber: 1,
    isPagination: false,
    isLoadingViaAPI: true,
    currentDataFromServer: [],
    allDataWhosePagesHaveBeenLoaded: []

};

export const fetchPage = createAsyncThunk('user/fetchPage', async (url, thunkAPI) => {

    try {
        const res = await axios.get(url);
        return res.data;
    } catch (error) {
        thunkAPI.dispatch(setError(error.message));
        throw error;
    }
});


const pagelSlice = createSlice({
    name: 'page',
    initialState,
    reducers: {

        currentPageNumber: (state, action) => {
            state.currentPageNumber = action.payload

        },
        ChangeCurrentNumberAftenLoadedPageWithHelpScroll: (state, action) => {

            state.currentPageNumber = action.payload

        },
        pagePagination: (state) => {
            if (!state.isPagination) {
                state.currentPageNumber = 1
                state.allDataWhosePagesHaveBeenLoaded[0] ? state.currentDataFromServer = state.allDataWhosePagesHaveBeenLoaded[0] : state.currentDataFromServer = state.currentDataFromServer
            }
            state.isPagination = !state.isPagination
        },
        openNewPage: (state, action) => {
            state.page_number = action.payload
        },
        fetchFirstPage: (state, action) => {
            state.allPagesServer = action.payload.info.pages
            state.currentDataFromServer = action.payload;
            state.allDataWhosePagesHaveBeenLoaded.push(action.payload);
            state.isLoadingViaAPI = false;
        }, saveCurrentPageInAllDataWhosePagesHaveBeenLoaded: (state, action) => {
            state.allDataWhosePagesHaveBeenLoaded.push(action.payload)

        }, changeCurrentPageFromPagination: (state, action) => {
            state.currentDataFromServer = action.payload;
        }
    },
    extraReducers: (builder) => {
        builder
            .addCase(fetchPage.pending, (state) => {
                state.isLoadingViaAPI = true;
            })
            .addCase(fetchPage.fulfilled, (state, action) => {
                state.isLoadingViaAPI = false;
                // state.currentPageNumber = getCurrentPageNumber(action.payload)
                state.currentDataFromServer = (action.payload)
            })
            .addCase(fetchPage.rejected, (state) => {
                state.isLoadingViaAPI = false;
            });
    },
});
export const selectAllPagesServer = (state) => state.page.allPagesServer
export const selectCurrentPageNumber = (state) => state.page.currentPageNumber
export const selectCurrentPage = (state) => state.page.currentDataFromServer
export const selectAllDataWhosePagesHaveBeenLoaded = (state) => state.page.allDataWhosePagesHaveBeenLoaded
export const selectIsPagination = (state) => state.page.isPagination;
export const selectIsLoading = (state) => state.page.isLoadingViaAPI;
export const { pagePagination, openNewPage, currentPageNumber, fetchFirstPage, saveCurrentPageInAllDataWhosePagesHaveBeenLoaded, changeCurrentPageFromPagination, ChangeCurrentNumberAftenLoadedPageWithHelpScroll } = pagelSlice.actions;
export default pagelSlice.reducer;
