import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Audio } from 'react-loader-spinner';
import { Switch, FormControlLabel, Button } from '@mui/material';
import axios from 'axios';


import {
  fetchPage,
  fetchFirstPage,
  selectIsLoading,
  selectAllDataWhosePagesHaveBeenLoaded,
  saveCurrentPageInAllDataWhosePagesHaveBeenLoaded,
  selectCurrentPage,
  selectIsPagination,
  pagePagination,
  ChangeCurrentNumberAftenLoadedPageWithHelpScroll
} from './redux/slices/pageSlice';

import {
  modalStatusChange,
  currentPersonageForWatch,
  changeCurrentWidthWindow
} from './redux/slices/modalSlice';

import Personage from './components/personage';
import KeepMountedModal from './components/KeepMountedModal';
import PaginationControlled from './components/Pagination';
import styles from './App.module.scss';
import { setError, selectError } from './redux/slices/errorSlice';
import ErrorPage from './pages/ErrorPage';
import getCurrentPageNumber from './utilites/getCurrentPageNumber';
import Error from './components/Error';

const api = await axios.get("https://rickandmortyapi.com/api/character/")
  .then((res) => res.data)
  .catch((err) => err)

function App() {



  // track changes in screen width
  window.onresize = () => dispatch(changeCurrentWidthWindow(document.body.offsetWidth))
  // Memoized selectors
  const allDataPage = useSelector(selectAllDataWhosePagesHaveBeenLoaded)
  const currentP = useSelector(selectCurrentPage)
  const paginationStatus = useSelector(selectIsPagination)
  const isLoading = useSelector(selectIsLoading)
  const dispatch = useDispatch()













  // for loading first page when user starts app
  const getFirstPage = () => {
    if (api.name !== "AxiosError") {
      dispatch(fetchFirstPage(api))
    } else {
      dispatch(setError())
    }
  }
  // checking, and whether this is the first launch and whether the first page needs to be loaded
  const loadFirstPage = useSelector((state) => (!!state.page.currentDataFromServer.info))
  if (!loadFirstPage) {
    getFirstPage()
  }


  // geting url next page
  const nextPageApi = useSelector((state) => (api.name !== "AxiosError") ? state.page.currentDataFromServer.info.next === null ? `https://rickandmortyapi.com/api/character/?page=${state.page.currentPageNumber}` : state.page.currentDataFromServer.info.next : null)

  // geting  current list for personages
  const listWithPers = useSelector((state) => state.page.currentDataFromServer.results)

  // checking status for MODAL WINDOW,  opened  or closed.
  const modalWindowStatus = useSelector((state) => state.modal.modalWindow)

  // when opened modal window geting current data for current personage
  const currentPersonageForModal = useSelector((state) => state.modal.currentPersonage)



  // find is next page in data saved in client
  const findNextPageInAllDataPage = (allDataPage.filter((page) => page.info.next !== nextPageApi));

  // function to check that the next page is saved or needs to be loaded from the server



  // fuction for opening modal window with current personage
  const openModalWindow = (eo) => {
    eo = eo || window.event
    eo.preventDefault()
    const currentEventId = (eo.target.id ? eo.target.id : eo.target.parentElement.id);

    let page = ''

    allDataPage.map((pages) => pages.results.filter((p) => +p.id === +currentEventId ? page = p : null))


    dispatch(currentPersonageForWatch(page))
    dispatch(modalStatusChange())
  }


  if (!paginationStatus) {
    const onScrool = window.onscroll = () => loadingNextPageIfScroll()
  }

  // for loading when scrool
  const loadingNextPageIfScroll = () => {
    if (api.name !== "AxiosError") {
      const height = document.body.offsetHeight
      const screenHeight = window.innerHeight
      const threshold = height - screenHeight
      const scrollPosition = window.scrollY || document.documentElement.scrollTop;

      if (threshold / 1.15 < scrollPosition || threshold / 1.30 > scrollPosition) {
      } else {
        dispatch(ChangeCurrentNumberAftenLoadedPageWithHelpScroll(getCurrentPageNumber(currentP)))
        fetchNextPage()
      }
    }
  }


  const fetchNextPage = () => {
    if (!findNextPageInAllDataPage.length == 0) {
      dispatch(fetchPage(nextPageApi))
      dispatch(saveCurrentPageInAllDataWhosePagesHaveBeenLoaded(currentP))
    } else {
      dispatch(fetchPage(nextPageApi))
    }


  }




  // ERRORS/////////////////////////////////////////////

  const selectErrorStatus = useSelector(selectError)
  if (api.name === "AxiosError") {

    return (
      <ErrorPage {...api} />
    )


  } else {
    return (

      <div className={styles.App}>
        {selectErrorStatus ? <Error {...api} /> : ''}
        <div className={styles.btnsTopAndPanding}>
          <div className={styles.btnToTop}> <Button buttontotop="btnToTop"
            onClick={() => {
              window.scrollTo({
                top: 0,
                left: 100,
                behavior: "smooth",
              });
            }} variant="contained" color="success">
            to top
          </Button></div>
          <div className={styles.pagination} onChange={() => { dispatch(pagePagination()) }}><FormControlLabel
            value="start"
            control={<Switch color="primary" />}
            label="pagination"
            labelPlacement="start"
          /></div>
          <div className={styles.paginationControlled}>
            {paginationStatus ? <PaginationControlled {...currentP} /> : null}
          </div>
        </div>

        <div className={styles.contaner}>

          {paginationStatus ? listWithPers.map((pers) => <Personage key={pers.id} id={pers.id} onClick={openModalWindow} namePersonage={pers.name} img={pers.image} />) :

            allDataPage.map((d) => d.results.map((pers) => <Personage key={pers.id} id={pers.id} onClick={openModalWindow} namePersonage={pers.name} img={pers.image} />))
          }
          {isLoading ? <Audio className={styles.AudioLoading}
            height="200"
            width="200"
            color="#4fa94d"
            ariaLabel="audio-loading"
            wrapperStyle={
              {
                zIndex: "100",
                position: "fixed",
                top: "50%",
                left: "50%",
                marginRight: "-50%",
                transform: " translate(-50%, -50%) "
              }
            }
            wrapperClass="wrapper-class"
            visible={true}

          /> : ''}
          {modalWindowStatus ? <KeepMountedModal {...currentPersonageForModal} /> : ''}

        </div>

      </div>
    );
  }
}

export default App;
