

const getCurrentPageNumber = (data) => {
    let numberNextPage = ''

    if (data.info.next === null) {

        for (let i = 0; i < data.info.prev.length; i++) {
            if (!isNaN(Number(data.info.prev[i]))) {
                numberNextPage += (data.info.prev[i]);
            }
        }
        return +numberNextPage + 1
    } else {
        let numberNextPage = ''
        for (let i = 0; i < data.info.next.length; i++) {
            if (!isNaN(Number(data.info.next[i]))) {
                numberNextPage += (data.info.next[i]);
            }
        }
        return +numberNextPage - 1
    }


}

export default getCurrentPageNumber
