import * as React from 'react';
import usePagination from '@mui/material/usePagination';
import { useDispatch, useSelector } from 'react-redux'
import { createTheme } from '@mui/material/styles';
import { green } from '@mui/material/colors';
import { selectCurrentPageNumber, fetchPage, selectAllPagesServer, currentPageNumber, changeCurrentPageFromPagination, selectCurrentPage, selectAllDataWhosePagesHaveBeenLoaded, saveCurrentPageInAllDataWhosePagesHaveBeenLoaded } from '../redux/slices/pageSlice'
import styles from "./Pagination.scss"
import { styled } from '@mui/material/styles';



export default function PaginationControlled({ info }) {
    const allDataPage = useSelector(selectAllDataWhosePagesHaveBeenLoaded)
    const currentP = useSelector(selectCurrentPage)
    const currentNumber = useSelector(selectCurrentPageNumber)
    const allPages = useSelector(selectAllPagesServer)
    const dispatch = useDispatch()
    const theme = createTheme({
        palette: {
            primary: green,
        }, shape: {
            borderRadius: "1rem"
        }
    });
    const List = styled('ul')({
        listStyle: 'none',
        padding: 0,
        margin: 0,
        display: 'flex',
        color: theme.palette.primary,
        backgroundColor: theme.palette.primary,
        borderRadius: theme.shape.borderRadius,
    });
    const { items } = usePagination({
        count: allPages,
    });




    const handleChange = (value) => {
        dispatch(currentPageNumber(value))
        const findNextPageInAllDataPage = (allDataPage.filter((page) => page.info.next === `https://rickandmortyapi.com/api/character/?page=${+value + 1}` || page.info.next === null));

        if (findNextPageInAllDataPage.length > 0) {

            dispatch(changeCurrentPageFromPagination(findNextPageInAllDataPage[0]))
        } else {

            dispatch(fetchPage(`https://rickandmortyapi.com/api/character/?page=${+value}`))
            dispatch(saveCurrentPageInAllDataWhosePagesHaveBeenLoaded(currentP))
        }


    };



    return (
        <nav component="pagination" >

            <List  >
                {items.map(({ page, type, selected, ...item }, index) => {
                    let children = null;

                    if (type === 'start-ellipsis' || type === 'end-ellipsis') {
                        children = '';
                    } else if (type === 'page') {
                        children = (
                            <button

                                type="button"
                                style={{
                                    fontWeight: selected ? 'bold' : undefined,
                                }}
                                {...item}
                            >
                                {page}
                            </button>
                        );
                    } else {
                        children = (
                            <button
                                type="button"  {...item}>
                                {type === 'previous' ? "<" : ">"}
                            </button>
                        );
                    }

                    return <li key={index} onClick={() => !selected ? handleChange(page) : ''}>{children}</li>;
                })}
            </List>

        </nav >
    );

}