import * as React from 'react';
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import Typography from '@mui/material/Typography';
import { useSelector, useDispatch } from 'react-redux'
import { IoCloseOutline } from "react-icons/io5";
import axios from 'axios'
import { modalStatusChange, selectCurrentWidthWindow, selectCurrentPersonage } from '../redux/slices/modalSlice';
import styles from './KeepMountedModal.module.scss'
import { setError } from '../redux/slices/errorSlice';





export default function KeepMountedModal(props) {

    const dispatch = useDispatch()
    const currentPersonage = useSelector(selectCurrentPersonage)
    const currentWidthWindow = useSelector(selectCurrentWidthWindow)
    const width = (currentWidthWindow === '' ? window.innerHeight : currentWidthWindow);
    const style = width < 750 ? {
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: '20.2rem',
        borderRadius: '9px',
        bgcolor: 'background.paper',
        boxShadow: 24,
        background: 'linear-gradient(to bottom,rgb(247, 245, 200), 40%, rgb(241, 241, 238))',
        filter: ' blur(1px)',
        p: 8.9,
    } : {
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: '45rem',
        borderRadius: '9px',
        bgcolor: 'background.paper',
        boxShadow: 24,
        background: 'linear-gradient(to right,rgb(247, 245, 200), 40%, rgb(241, 241, 238))',
        filter: ' blur(1px)',
        height: 320
    }
    const { name, status, episode, origin, species, type, location, gender, image } = currentPersonage
    const open = useSelector((state) => state.modal.modalWindow)
    const handleClose = () => dispatch(modalStatusChange());
    const [firstEpisode, setFirstEpis] = React.useState('');


    const fetchFirstEpisode = axios.get(episode[0]).then((res) => setFirstEpis(res.data.name)).catch((error) => setError(error.message))

    return (
        <div>
            <Modal
                keepMounted
                open={open}
                onClose={handleClose}
                aria-labelledby="keep-mounted-modal-title"
                aria-describedby="keep-mounted-modal-description"
            >
                <Box sx={style}>
                    <IoCloseOutline onClick={handleClose} className={styles.closeModalX} />
                    <img className={styles.imgCurrentPersonage} src={image} alt={name} />
                    <br />
                    <Typography id="keep-mounted-modal-description" sx={width < 750 ? { mt: 30, ml: 0 } : { mt: 5, ml: 45 }}>
                        <strong>  Name:{` ${name}`}</strong>
                        <br />
                        Gender: <strong> {`${gender}`}</strong>
                        <br />
                        Status: <strong>{`${status}`}</strong>
                        <br />
                        Species: <strong>{`${species}`}</strong>
                        <br />
                        Type: <strong>{`${type === '' ? 'unknow' : type}`}</strong>
                        <br />
                        Location: <strong>{`${location.name}`}</strong>
                        <br />
                        Origin: <strong>{`${origin.name}`}</strong>
                        <br />
                        First episode: <strong>{`${firstEpisode}`}</strong>
                    </Typography>
                </Box>
            </Modal>
        </div>
    );
}