import { createSlice } from '@reduxjs/toolkit'

const initialState = {
    status: false,
    errorMessange: '',

}


const errorSlice = createSlice({
    name: 'error',
    initialState,
    reducers: {
        setError: (state, action) => {
            state.status = !state.status
            state.errorMessange = `ERROR ${action.payload}
             Please, try again later`
        },
        cleanError: (state, action) => {
            state.status = !state.status
            state.errorMessange = ''
        },
    }
})

export const selectError = (state) => state.error.status
export const selectErrorMessange = (state) => state.error.errorMessange
export const { setError, cleanError } = errorSlice.actions
export default errorSlice.reducer