import React from 'react';
import { Box, Typography } from '@mui/material';
import { blue, red } from '@mui/material/colors';
import SentimentVeryDissatisfiedIcon from '@mui/icons-material/SentimentVeryDissatisfied';

const primary = { blue: blue[50], red: red[500] };

export default function ErrorPage(error) {
    const { code, message, config } = error;
    return (
        <Box
            sx={{
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                minHeight: '100vh',
                backgroundColor: primary.blue,

            }}
        >
            <Typography variant="h1" style={{ color: primary.red }}>

                <SentimentVeryDissatisfiedIcon fontSize="large" /> <br />  Sorry, but you have a problem: <br />{code} <br />{message}<br />{config.url}
            </Typography>
        </Box>
    );
}