import { createSlice } from '@reduxjs/toolkit'

const initialState = {
    modalWindow: false,
    currentWidthWindow: '',
    currentPersonage: '',

}


const modalSlice = createSlice({
    name: 'modal',
    initialState,
    reducers: {
        modalStatusChange: (state) => {
            let modalStatus = state.modalWindow
            state.modalWindow = !modalStatus
        }
        ,
        changeCurrentWidthWindow: (state, action) => {

            state.currentWidthWindow = action.payload
        },
        currentPersonageForWatch: (state, action) => {

            state.currentPersonage = action.payload
        }
    }
})

export const selectCurrentPersonage = (state) => state.modal.currentPersonage
export const selectCurrentWidthWindow = (state) => state.modal.currentWidthWindow
export const { modalStatusChange, currentPersonageForWatch, changeCurrentWidthWindow } = modalSlice.actions
export default modalSlice.reducer